const URL = 'http://192.168.1.48:9080/api';
//const URL = 'http://editor.gopopon.com/api';

const STATIC = false;

let API_URL = {

};

if (!STATIC) {
    API_URL = {
         domain: 'http://192.168.1.48:80/h5_editor/frontend/',
        //domain: 'http://editor.gopopon.com/upload/',
        course: {
            save: `${URL}/course`,
            show: `${URL}/course/`,
        },
        template: {
            save: `${URL}/template`,
            list: `${URL}/templates`,
            show: `${URL}/template/`,
        },
        category: {
            list: `${URL}/categories`,
        },
        audio: {
            list: `${URL}/musics/all`,
            upload: `${URL}/upload/music`,
        },
        image: {
            list: `${URL}/images`,
            upload: `${URL}/upload/image`,
        },
    };
}

export { API_URL as default };



