var common = {};

common.getAccessToken = function(){
    function getQueryString(name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
        var r = window.location.search.substr(1).match(reg);
        if (r != null) return unescape(r[2]); return null;
    }
    var accessToken = sessionStorage.token;
    if(!accessToken){
        accessToken = getQueryString("access_token");
    }
    // return accessToken;
    return 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJkYXRldGltZSI6MTUwODY3MDE0ODg1OSwicm9sZSI6IueuoeeQhuWRmCIsImlkIjoxLCJwaG9uZSI6IjE4MDE1MzUxODE5In0.2qT87dGmgZN-Ja_9YpJb-b35TXo6P9wxZZJSlaQ2mT8';
};

export {common as default}



