import React from 'react';
import Swiper from 'swiper';
import { connect } from 'react-redux';
import 'swiper/dist/css/swiper.css';
import store from '../../store';
import { changeCourse } from '../../actions/h5Actions';
import Page from './elements/Page';
import Fetch from '../../common/FetchIt';
import API_URL from '../../common/url';

import './viewer.less';

class Viewer extends React.Component {
    state = {
        pages: [],
    };
    componentDidMount = () => {
        this.loadData();
    };

    loadData = () => {
        const id = this.props.params.id;
        if (!id) {
            const pages = window.pages;
            store.dispatch(changeCourse(0, pages));
            this.initSwiper();
        } else {
            Fetch.get(`${API_URL.course.show}${id}`).then(course => {
                const pages = JSON.parse(course.pages);
                store.dispatch(changeCourse(course.id, pages));
                this.initSwiper();
            });
        }
    };
    initSwiper = () => {
        new Swiper('.swiper-container', {
            loop: true,
        });
    };
    render() {
        return (
            <div className="swiper-container">
                <div className="swiper-wrapper">
                    {
                        this.props.pages.map((page, index) => <div className="swiper-slide" key={page.id}><Page page={page} viewing /></div>)
                    }
                </div>
            </div>
        );
    }
}
const mapStateToProps = function (store) {
    return {
        pages: store.h5State.present.pages,
    };
};

export default connect(mapStateToProps)(Viewer);



